import { Component, OnInit, TemplateRef } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { DataTableResource } from 'angular7-data-table';

import { LanguageService } from '../../services/language.service';
import { Subscription } from 'rxjs';

import { LeadersService } from './leaders.service';
import { GroupsService } from '../groups/groups.service';
import { InputSelectItem } from 'src/app/models/InputSelectItem';

import {
  ExportAsService,
  ExportAsConfig,
  SupportedExtensions,
} from 'ngx-export-as';

@Component({
  selector: 'app-leaders',
  templateUrl: './leaders.component.html',
  styleUrls: ['./leaders.component.scss'],
})
export class LeadersComponent implements OnInit {
  itemResource;
  items = [];
  itemCount = 0;
  limit = 20;
  limits = [20, 25, 50];
  offset = 1;
  tableElements = [];

  modalRef: any;

  private langSub: Subscription;
  currentLang;
  arabic: boolean;

  constructor(
    private modalService: BsModalService,
    private languageService: LanguageService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private leadersService: LeadersService,
    private groupsService: GroupsService,
    private exportAsService: ExportAsService
  ) {
    this.langSub = this.languageService.currentLanguage.subscribe((lang) => {
      this.currentLang = lang;
      if (this.currentLang && this.currentLang === 'ar') {
        this.arabic = true;
      } else {
        this.arabic = false;
      }
    });
  }
  ngOnDestroy(): void {
    this.langSub.unsubscribe();
  }

  ngOnInit(): void {
    this.spinner.show();

    this.leadersService.getAll();
    this.leadersService.leaders.subscribe(async (data) => {
      if (data) {
        this.items = data;

        this.itemResource = new DataTableResource(data);
        this.itemCount = await this.itemResource.count();
        this.reloadItems({
          offset: 0,
          limit: this.limit,
        });
      }
    });

    this.spinner.hide();
  }

  async reloadItems(event) {
    if (this.itemResource) {
      this.tableElements = await this.itemResource.query(event);
    }
  }

  async FilterRecords(FilterRecord) {
    this.tableElements = this.items;
    if (FilterRecord !== '') {
      this.tableElements = this.items.filter(
        (item) =>
          item.name.toLowerCase().includes(FilterRecord) ||
          item.leaderId?.group.name.toLowerCase().includes(FilterRecord)
      );
    }

    this.itemResource = new DataTableResource(this.tableElements);
    this.itemCount = await this.itemResource.count();
    this.reloadItems({
      offset: 0,
      limit: this.limit,
    });
  }

  // Start select dropdown
  selectGroupsList: InputSelectItem[];
  selectedGroupId: string;
  getGroupsList() {
    this.spinner.show();

    this.groupsService.getAll();

    this.groupsService.groups.subscribe((data) => {
      if (data) {
        this.selectGroupsList = data.map((x) => {
          return {
            _id: x['_id'],
            nameEn: `${x['name']}`,
            nameAr: ``,
          };
        });
      }
    });

    this.spinner.hide();
  }
  selectGroup(event) {
    if (event) {
      this.selectedGroupId = event._id;
    }
  }
  // End select dropdown

  itemId;
  openModal(template: TemplateRef<any>, id) {
    this.itemId = id;
    this.modalRef = this.modalService.show(template);
  }

  addLeader() {
    this.spinner.show();

    this.modalRef.hide();
    this.leadersService.addLeader(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.leadersService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  deleteLeader() {
    this.spinner.show();

    this.modalRef.hide();
    this.leadersService.deleteLeader(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.leadersService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  addLeaderToGroup() {
    this.spinner.show();

    this.modalRef.hide();
    this.leadersService
      .addLeaderToGroup(this.itemId, this.selectedGroupId)
      .subscribe(
        (res) => {
          console.log(res);
          this.toastr.info(res['message'], 'Info');
          this.items = [];

          this.leadersService.getAll();
        },
        (error) => {
          console.log(error);
          this.toastr.error(error.error.message, 'Error');
          // this.error.next(error.error.message);
        }
      );

    this.spinner.hide();
  }

  lockElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.leadersService.lockOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.leadersService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  unlockElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.leadersService.unlockOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.leadersService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  exportAsConfig: ExportAsConfig = {
    type: 'pdf',
    elementIdOrContent: 'my-table',
    options: {
      jsPDF: {
        orientation: 'landscape',
      },
    },
  };
  exportAs(type: SupportedExtensions, opt?: string) {
    this.exportAsConfig.type = type;
    if (opt) {
      this.exportAsConfig.options.jsPDF.orientation = opt;
    }
    this.exportAsService
      .save(this.exportAsConfig, 'Presidents')
      .subscribe(() => {
        // save started
      });
    this.exportAsService.get(this.exportAsConfig).subscribe((content) => {
      console.log(content);
    });
  }
}
